html {
  scroll-behavior: smooth;
}
.top-menu {
  position: relative;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 104px;

  width: 100vw;
}
.top-menu > span {
  position: relative;
  margin-left: 54px;
  font-size: small;
  font-weight: bold;
}
.top-menu > img {
  position: relative;
  width: 104px;
  height: 104px;
  margin-left: 50px;
}
.beauty,
.fitness {
  position: relative;
  height: 590px;
  width: 100vw;
  background-image: url(../pictures/keira-knightley.png);
  background-repeat: no-repeat;
  background-size: 950px;
  box-sizing: border-box;
  background-color: black;
  transition: width 2s 2s, height 2s 2s, opacity 2s;
  animation: mymove 3s;
  @keyframes mymove {
    from {
      background-size: 965px;
    }
    to {
      background-size: 950px;
    }
  }
}
.fitness {
  animation: my-move 3s;

  background-image: url(../pictures/fit.jpg);
  background-size: 1520px;
  @keyframes my-move {
    from {
      background-size: 1535px;
    }
    to {
      background-size: 1520px;
    }
  }
}

.test {
  height: 590px;
  width: 680px;
  position: relative;
  transition: width 2s 2s, height 2s 2s, opacity 2s;
}
body {
  overflow-y: scroll;
  height: 500vh;
  position: relative;
  overflow-x: hidden;
}

.insider {
  width: 96vw;
  height: fit-content;
  margin-top: -200px;
  background-color: transparent;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  overflow-x: auto;
  padding-bottom: 40px;
  margin-bottom: 100px;
}

.product-container {
  width: 220px;
  box-sizing: border-box;
  height: 470px;
  margin-left: 50px;

  display: flex;
  flex-shrink: 0;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  border-radius: 15px;
}
.product-container:hover {
  background-color: white;
  position: relative;
  box-shadow: 0px 3px 16px 2px rgba(128, 128, 128, 0.322);
  transition: width 0.5s 0.5s, height 0.5s 0.5s, background-color 0.5s;
}

.product-description {
  margin-top: 10px;
}
.product1,
.product2,
.product3,
.product4 {
  height: 250px;
  width: 50%;
  background-size: 120%;
  background-repeat: no-repeat;
  background-position-y: bottom;
  background-position-x: center;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  position: relative;
  align-items: center;
}
.product2 {
  background-image: url("../pictures/p2.png");
}
.product1 {
  background-image: url("../pictures/p1.png");
}
.product3 {
  background-image: url("../pictures/p3.png");
}
.product4 {
  background-image: url("../pictures/p4.png");
}
.fade-in {
  flex-shrink: 0;
  width: 40vw;
  padding-left: 150px;
  box-sizing: border-box;
  padding-top: 170px;
  font-size: 50px;
  opacity: 1;
  transition: width 2s 2s, height 2s 2s, opacity 2s;
}
.fade-out {
  opacity: 0;
  flex-shrink: 0;
  width: 40vw;
  padding-left: 150px;
  box-sizing: border-box;
  padding-top: 170px;
  font-size: 50px;
  transition: width 2s 2s, height 2s 2s, opacity 2s;
}
.lazy {
  background-color: chartreuse;
  height: 590px;
  width: 680px;
  // transform: scale(1.1);

  // transition: transform 3s;
}
